import getOffsetParent from '../calc/getOffsetParent';
import getVisibleRectForElement from '../calc/getVisibleRectForElement';
import { getRectOfSource, getRectOfTarget, getDestinationRect, alignRect } from './alignRect';
import type { IAlign } from '../interface';

/**
 * 
 * @param el: sourceNode, 内容节点
 * @param refNode: targetNode, 触发器节点
 * @param align: 对齐的配置
 * @returns 
 */
function alignElement(el: HTMLElement, refNode: HTMLElement, align: IAlign) {
  const srcInfo = getRectOfSource(el, align);
  const tgtInfo = getRectOfTarget(refNode, align);
  const destRect = getDestinationRect(srcInfo, tgtInfo, align);

  alignRect(el, destRect, align);
  return destRect;
}

alignElement.__getOffsetParent = getOffsetParent;

alignElement.__getVisibleRectForElement = getVisibleRectForElement;

export default alignElement;
